body {
  font-family: "eesti";
  font-size: 16px;
  background: #fdfdfd;
}
.red-shadow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: scale(1) translate3d(-50%, -50%, 0);
  background-color: #31426f;
  // overflow: hidden;
  z-index: 1;
  // transition: transform ease 0s 100ms;
  border-radius: 50%;
}
.avatar-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  overflow: hidden;
  // border: 30px pink solid;
  // background-color: rgb(209, 108, 125);
  border-radius: 50%;
  z-index: 100;
}
.avatar {
  position: relative;
  border-radius: 50%;
  width: 50vh;
}
